import React from 'react';

import { HiArrowNarrowRight, HiTicket } from 'react-icons/hi';
import tik from '../Sprites/tic.png';
import linkss from '../Sprites/link.png';

const tic = () =>{
  window.location = "https://tictactoee.tk";
}
const Project = () => {

  return (
    <div name='proj' className='w-full h-screen align-middle justify-center text-center text-black-300'>
      {/* Container */}
      <div style={{marginTop:'15%'}} className='max-w-[1000px]  mx-auto p-4 flex flex-col justify-center w-full h-full'>
          <div>
              <p style={{color:'#FA9400'}} className='text-6xl font-bold inline border-b-4 border-pink-600 '>Projects</p>
          </div>

          <div className='w-full flex flex-wrap justify-center flex-row text-center py-8'>
              <div style={{width:'480px',height:'120px',marginTop:'25px',marginRight:'5px'}} className=' hover:scale-110 duration-500'>
                  <img style={{width:'55px',height:'35px'}} src={tik} className='w-20 mx-auto'/>
                  <p style={{fontSize:'19px'}} className='my-4'>TIC TAC TOE</p>
                  <p  className='my-4'>Tic-tac-toe, noughts and crosses, or Xs and Os is a paper-and-pencil game for two players who take turns marking the spaces in a three-by-three grid with X or O.</p>
                  <button style={{cursor:'pointer',border:'1px solid black',width:'90px',height:'30px'}} onClick={tic}>Visit Site</button>
                  </div>
                  <div style={{width:'480px',height:'120px',marginTop:'180px',marginRight:'5px'}} className=' hover:scale-110 duration-500'>
                  <img style={{width:'55px',height:'35px'}} src={linkss} className='w-20 mx-auto'/>
                  <p style={{fontSize:'19px'}} className='my-4'>URL Shortener</p>
                  <p  className='my-4'>URL shortening is a technique on the World Wide Web in which a Uniform Resource Locator may be made substantially shorter and still direct to the required page.</p>
                  <button style={{cursor:'pointer',border:'1px solid black',width:'150px',height:'30px'}} onClick={alert.bind(this,"Coming Soon!")}>In Development!</button>
                  </div>
              </div>
              
          </div>
      </div>
     
  );
};

export default Project;