import React from 'react'

class Sender extends React.Component{
  sendHook(firstname,lastname,email,message){
    fetch(
        'api',
        {
          method: 'post',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            
                "content": null,
                "embeds": [
                  {
                    "title": "-------Contact Info-------",
                    "description": "First Name : "+firstname+"\nLast Name : "+lastname+"\nEmail: "+email,
                    "color": 5570304,
                    "fields": [
                      {
                        "name": "Message",
                        "value": message
                      }
                    ],
                    "author": {
                      "name": "Rayan Official",
                      "url": "https://rayanofficial.me"
                    },
                    "footer": {
                      "text": "Status : Connected 🟢"
                    }
                  }
                ],
                "attachments": []
              }
          ),
        }
      )
 }
}

export default Sender