import React from 'react'
import { HiArrowNarrowRight } from 'react-icons/hi';

import { Html } from '@mui/icons-material';
import htmlcssjsicon from '../Sprites/html.png';
import reacticon from '../Sprites/react.png';
import unity from '../Sprites/unity.png';
import cs from '../Sprites/net.png';
const MySkills = () => {
  return (
     <div name='skills' style={{marginBottom:'200px'}} className='w-full  h-screen align-middle justify-center text-center text-black-300'>
      {/* Container */}
      <div style={{marginTop:'15%'}} className='max-w-[1000px]  mx-auto p-4 flex flex-col justify-center w-full h-full'>
          <div>
              <p style={{color:'#FA9400'}} className='text-6xl font-bold inline border-b-4 border-pink-600 '>Skills</p>
          </div>
          <center>
          <div  className='w-full  flex flex-wrap justify-center flex-row text-center py-8'>
           
              <div  style={{backgroundColor:'white',width:'480px',height:'120px',marginTop:'25px'}} className=' hover:scale-110 duration-500'>
                  <img style={{width:'55px',height:'35px'}} src={htmlcssjsicon} className='w-20 mx-auto'/>
                  <p style={{fontSize:'19px'}} className='my-4'>HTML, CSS, JAVASCRIPT</p>
                  <div style={{width:'98%',height:'20px',backgroundColor:'#E5E5E5'}}>
                  <div style={{width:'98%',height:'20px',backgroundColor:'#FBD214'}}></div>
                  </div>
              </div>
              <div style={{backgroundColor:'white',width:'480px',height:'120px',marginTop:'25px'}} className=' hover:scale-110 duration-500'>
                  <img style={{width:'35px',height:'35px'}} src={reacticon} className='mx-auto'/>
                  <p style={{fontSize:'19px'}} className='my-4'>React Js</p>
                  <div style={{width:'98%',height:'20px',backgroundColor:'#E5E5E5'}}>
                  <div style={{width:'92%',height:'20px',backgroundColor:'#FBD214'}}></div>
                  </div>
              </div>
              <div style={{backgroundColor:'white',width:'480px',height:'120px',marginTop:'25px'}} className=' hover:scale-110 duration-500'>
              <img style={{width:'35px',height:'35px'}} src={unity} className='w-20 mx-auto'/>
                  <p style={{fontSize:'19px'}} className='my-4'>Unity Game Dev</p>
                  <div style={{width:'98%',height:'20px',backgroundColor:'#E5E5E5'}}>
                  <div style={{width:'50%',height:'20px',backgroundColor:'#FBD214'}}></div>
                  </div>
              </div>
              <div style={{backgroundColor:'white',width:'480px',height:'120px',marginTop:'25px'}} className=' hover:scale-110 duration-500'>
              <img style={{width:'35px',height:'35px'}} src={cs} className='w-20 mx-auto'/>
                  <p style={{fontSize:'19px'}} className='my-4'>C# .NET</p>
                  <div style={{width:'98%',height:'20px',backgroundColor:'#E5E5E5'}}>
                  <div style={{width:'95%',height:'20px',backgroundColor:'#FBD214'}}></div>
                  </div>
              </div>
              
          </div>
          </center>
      </div>
    </div>
     
  )
}

export default MySkills