import React from 'react'
import address from '../Sprites/add.png';
import em from '../Sprites/email.png';
import Sender from './Sender';
import { useState } from 'react';
const SenderApi = new Sender();

const Contact = () => {
  const [FirstName, setFirstName] = useState('');
  const [LastName, setLastName] = useState('');
  const [Email, setEmail] = useState('');
  const [Message, setMsg] = useState('');
  return (
    <div name='con' className='w-full  h-screen align-middle justify-center text-center text-black-300'>
    {/* Container */}
    <div style={{marginTop:'15%'}} className='max-w-[1000px]  mx-auto p-4 flex flex-col justify-center w-full h-full'>
        <div>
            <p className='text-6xl font-bold inline border-b-4 border-pink-600 text-yellow-500 '>Contact</p>
        </div>

        <div className='w-full flex flex-wrap justify-center flex-row text-center py-8'>
           
        <div style={{width:'500px',cursor:'pointer',borderRadius:'20px',height:'350px',border:'1px solid black',marginTop:'25px',marginRight:'5px'}} className=''>
           <label>
           <h5 style={{"float":'left','marginLeft':'20%',marginTop:'30px'}}>First Name: </h5><input onChange={(e)=>{setFirstName(e.target.value)}} style={{"float":'left','marginLeft':'10px',marginTop:'30px',borderBottom:'1px solid black',outline:'none'}} type="text" /><br /><br />
           </label>
           <label>
           <h5 style={{"float":'left','marginLeft':'20%',marginTop:'30px'}}>Last Name: </h5><input onChange={(e)=>{setLastName(e.target.value)}} style={{"float":'left','marginLeft':'10px',marginTop:'30px',borderBottom:'1px solid black',outline:'none'}} type="text" /><br /><br />
           </label>
           <label>
           <h5 style={{"float":'left','marginLeft':'27.5%',marginTop:'30px'}}>Email: </h5><input onChange={(e)=>{setEmail(e.target.value)}} style={{"float":'left','marginLeft':'10px',marginTop:'30px',borderBottom:'1px solid black',outline:'none'}} type="text" /><br /><br />
           </label>
           <label>
           <h5 style={{"float":'left','marginLeft':'16%',marginTop:'30px'}}>Your Message: </h5><textarea onChange={(e)=>{setMsg(e.target.value)}} style={{"float":'left','height':'50px','marginLeft':'10px',marginTop:'30px',borderBottom:'1px solid black',outline:'none'}} type="text" /><br /><br />
           <button onClick={SenderApi.sendHook.bind(this,FirstName,LastName,Email,Message)} style={{border:'1px solid #000',width:'100px',height:'30px',"float":'left','marginLeft':'-30%',borderRadius:'10px',marginTop:'90px'}} className='text-black  items-center hover:text-white hover:bg-blue-600 hover:border-blue-600'>
            Submit
            <span className='group-hover:rotate-90 duration-300'>
      
            </span>
          </button>
           </label>
           
           
          
            </div>
        </div>
    </div>
  </div>
  )
}
/* <div style={{width:'300px',cursor:'pointer',borderRadius:'20px',height:'120px',border:'1px solid black',marginTop:'25px',marginRight:'5px'}} className=''>
                <img style={{width:'65px',height:'70px'}} src={em} className='w-20 mx-auto'/>
                <p style={{fontSize:'19px',marginTop:'-15px',marginBottom:'-1px'}} className='my-4'>Email</p>
                <p>r4yan.acc@gmail.com</p>
            </div>
    
            <div style={{width:'300px',cursor:'pointer',borderRadius:'20px',height:'120px',border:'1px solid black',marginTop:'25px',marginRight:'5px'}} className=''>
                <img style={{width:'45px',height:'35px',marginTop:'15px'}} src={address} className='w-20 mx-auto'/>
                <p style={{fontSize:'19px',marginBottom:'-1px'}} className='my-1'>Address</p>
                <p>Mohakhali, Dhaka, Bangladesh</p>
            </div>
            ?*/
export default Contact