import React, { useState } from 'react';
import { AiOutlineMenu ,AiTwotoneCloseCircle} from "react-icons/ai";
import { Link } from 'react-scroll';
import dev from '../Sprites/dev.png';
const reload = () =>{
  window.location.reload();
}
const Header = () => {
  const clickHandler=()=>{
    showPcView?setshowPcView(false):setshowPcView(true);
  }
  const [showPcView, setshowPcView] = useState(false);
  return (
    <div className='fixed w-full h-[80px] z-50 flex justify-between items-center px-4 bg-[#fff] opacity-100 shadow-sm  text-black-300 '>
     
      <div>
      <h1 title='Rayan Official Site' className='text-lg cursor-pointer italic font-sans' onClick={reload}><img style={{width:'150px',height:'150px'}} src={dev} alt="" /></h1>
      </div>
     
    
      <ul className='hidden md:flex'>
        <li id='pcViewNavBTN' className='mr-5 cursor-pointer hover:scale-110  duration-500'>
           <Link  to='home' className=''  smooth={true} duration={500}>
            Home
          </Link>
        </li>
        <li id='pcViewNavBTN' className='mr-5 cursor-pointer hover:scale-110 duration-500'>
          <Link to='about' smooth={true} duration={500}>
           About Me
          </Link>
        </li>
        <li id='pcViewNavBTN' className='mr-5 cursor-pointer hover:scale-110 duration-500'>
          <Link to='proj' smooth={true} duration={500}>
           Recent Project
          </Link>
          </li>
        <li id='pcViewNavBTN' className='mr-5 cursor-pointer hover:scale-110 duration-500'>
        <Link to='con'smooth={true} duration={500}>
        Contact
          </Link>
        </li>
      </ul>
     
     {/*Hidden Menu*/}
     <div className='md:hidden z-10 mr-5 cursor-pointer' onClick={clickHandler}>
      {
        showPcView?<h1 id='x' className='font-sans text-2xl text-red-600'>X</h1>:<AiOutlineMenu></AiOutlineMenu>
      }
     </div>
     
     {/*Mobile View Menu*/}
     <ul  className={!showPcView?'hidden':'absolute top-0 left-0 w-full h-screen bg-[#0a192f] flex flex-col justify-center items-center'}>
        <li id='mobNavView' className='py-6 text-3xl text-blue-600 cursor-pointer'>
          <Link to='home' onClick={clickHandler} smooth={true} duration={500}>
            Home
          </Link>
        </li>
        <li id='mobNavView' className='py-6 text-3xl text-blue-600 cursor-pointer'>
          <Link to='about' onClick={clickHandler} smooth={true} duration={500}>
           About Me
          </Link>
        </li>
        <li id='mobNavView' className='py-6 text-3xl text-blue-600 cursor-pointer'> 
          <Link to='proj' onClick={clickHandler} smooth={true} duration={500}>
           Recent Project
          </Link></li>
        <li id='mobNavView' className='py-6 text-3xl text-blue-600 cursor-pointer'>
        <Link to='con' onClick={clickHandler} smooth={true} duration={500}>
        Contact
          </Link>
        </li>
      </ul>

    </div>
  );
};

export default Header;